export default {
	data() {
		return {
			paramsArr: [
				{
					name: '填报位置',
					key: 'field02'
				},
				{
					name: '是否需要援助',
					key: 'field06'
				},
				{
					name: '是否受伤',
					key: 'field07'
				},
				{
					name: '语音信息',
					key: 'field08'
				},
				{
					name: '求助具体要求',
					key: 'field09'
				}
			],
			dataArr: []
		};
	},
	mounted(){
		this.getData()
	},
	methods: {
		handleCollapse(item, index) {
			this.$set(item, 'isActive', !item.isActive);
		},
		getData(){
			this.$HTTP({
				url:'/getHelp/list',
				params:{
					"field01":uni.getStorageSync('userId'),
					"pageNum":"",
					"pageSize":""
				},
				successCallback:({data})=>{
					if(data.code == 0){
						let arr = data.data.list.map(item=>{
							return{
								...item,
								title:new Date(item.addTime).Format('yyyy-MM-dd')
							}
						})
						this.dataArr = arr
					}else{
						uni.showToast({
							title:data.msg,
							icon:'none'
						})
					}
				}
			})
		},
	}
};
